.interactionBar {
    display: flex;
    justify-content: space-around;
    bottom: 0;
    width: 100%;
    height: 38px;
    border-radius: 0px 0px 8px 8px;
    background-color: #f5f5f5;
}

.interactionBar button, .interactionBar .dropDownButton {
    flex: 1;
    font-weight: normal;
    font-size: 14px;
    color: #2dbfbf;
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
    user-select: none;
}

.dropDownButton .button {
    /* display: flex; */
    height: 100%;
    width: 100%;
}

.dropdown-menu {
    display: none;
}

.dropdown-menu.show {
    display: flex;
    flex-direction: column;
    background-color: #f5f5f5;
    padding: 5px 10px;
    border-radius: 0 8px 8px 8px;
    z-index: 10;
}