@import url(https://fonts.googleapis.com/css?family=Heebo:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.mobileNavBar {
    position: fixed;
    z-index: 10;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-sizing: border-box;
    width: 100%;
    height: 124.33px;
    border-bottom: 0.5px solid #bcc6cf;
    /* Note: currently only Safari supports backdrop-filter */
    -webkit-backdrop-filter: blur(50px);
            backdrop-filter: blur(50px);
    --webkit-backdrop-filter: blur(50px);
    background-color: rgba(255, 255, 255, 0.98);
    /* (plus shape's fill blended on top as a separate layer with 67.62% opacity) */
}

.row {
    flex-direction: row;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.mobileNavBar .row:last-of-type{
    overflow-x: scroll;
    max-width: 100%;
    justify-content: start;
    margin: 0 auto;
}

.rowAccessory {
    display: flex;
    flex-direction: row;
    flex-grow: 1.5;
}

.rowAccessory.left {
    padding-left: 30px;
}

.rowAccessory.right {
    padding-right: 30px;
}

.row h3{
    margin: 0 15px;
    padding: 5px 10px;
    font-weight: 600;
    font-size: 1.25em;
    text-align: center;
    color: #2dbfbf;
    border-bottom: solid #ffffff00;
    white-space: nowrap;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
        user-select: none;
}

.row h3.active{
    border-bottom: solid;
}

.profile {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.searchBar {
    flex-grow: 1;
    width: 645px;
    height: 36px;
    border-radius: 17px;
    border: none;
    background: #e7ecf0;
    text-align: center;
    font-weight: normal;
    font-size: 1.0625em;
    /* font-size: 20px; */
    color: #647586;
    outline: none;
    box-sizing: border-box;
}

.searchBar:focus {
    border: solid 2px #2dbfbf;
}

/* #region Media Queries */

@media only screen and (max-width: 500px), screen and (max-height: 500px) {

    .mobileNavBar {
        height: 90px;
    }

    .mobileNavBar .row:first-of-type {
        margin-top: 5px;
    }

    .rowAccessory.left {
        padding-left: 20px;
        padding-right: 10px;
    }
    
    .rowAccessory.right {
        padding-right: 20px;
        padding-left: 10px;
    }
    
}

/* #endregion */
.activityBar {
    display: flex;
    padding-bottom: 5px;
    white-space: nowrap;
}

.featuredEndorsers {
    position: relative;
    width: 50px;
    height: 20px;
}

.activityBar .icon {
    font-size: 18px;
    color: #2dbfbf;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.featuredEndorsers .icon{
    font-size: 20px;
    z-index: 6;
    position: absolute;
}

.featuredEndorser {
    height: 100%;
    border-radius: 50%;
    position: absolute;
    left: 28px;
    top: 0;
    z-index: 2;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.featuredEndorser:nth-of-type(1) {
    left: 14px;
    z-index: 4;
}

.endorserCover {
    height: 100%;
    width: 19.58px;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 16px;
    background-color: white;
    z-index: 3;
}

.endorserCover:nth-of-type(1) {
    height: 90%;
    top: -2px;
    left: 2px;
    z-index: 5;
}

.activityBar h5 {
    margin: 0;
    font-weight: 500;
    font-size: 15px;
    text-align: left;
    color: #000;
}

.activityBar h5:first-of-type {
    padding-right: 8px;
}

.ampersand {
    font-weight: 300;
}
.tags {
    padding-top: 5px;
    padding-bottom: 10px;
}

.tag{
    font-weight: normal;
    font-size: 14px;
    text-align: center;
    line-height: 24px;
    border-radius: 12px;
    display: inline-block;
    padding: 0 5px;
    margin-right: 3px;
}

.beginner {
    color: #16390a;
    background-color: #D0F7C5;
}

.intermediate {
    color: #422c06;
    background-color: #ffeab6;
}

.advanced {
    color: #6B2613;
    background-color: #FDE2D5;
}
.cardContent {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    margin: 0 10px;
}

.cardTitle {
    margin: 0;
    padding: 10px 0 3px 0;
    font-weight: bold;
    font-size: 1em;
    text-align: left;
    color: #000;
    line-height: 20px;
    cursor: pointer;
}

.cardTitle:hover {
    text-decoration: underline;
}

.cardDescription {
    margin: 0;
    flex: 1 1;
    font-weight: 300;
    line-height: 18px;
    font-size: 0.8em;
    text-align: left;
    color: #000;
}
.interactionBar {
    display: flex;
    justify-content: space-around;
    bottom: 0;
    width: 100%;
    height: 38px;
    border-radius: 0px 0px 8px 8px;
    background-color: #f5f5f5;
}

.interactionBar button, .interactionBar .dropDownButton {
    flex: 1 1;
    font-weight: normal;
    font-size: 14px;
    color: #2dbfbf;
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.dropDownButton .button {
    /* display: flex; */
    height: 100%;
    width: 100%;
}

.dropdown-menu {
    display: none;
}

.dropdown-menu.show {
    display: flex;
    flex-direction: column;
    background-color: #f5f5f5;
    padding: 5px 10px;
    border-radius: 0 8px 8px 8px;
    z-index: 10;
}
.card {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 30%;
    min-width: 300px;
    height: 462px;
    border-radius: 8px;
    margin: 10px;
    background: #fff;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
}

.cardPreview {
    position: relative;
}

.cardPreview img {
    width: 100%;
    border-radius: 8px 8px 0 0;
    height: 175px;
    object-fit: cover;
}

.cardPreview .icon {
    position: absolute;
    font-size: 30px;
    bottom: 10px;
    right: 10px;
    color: rgba(255, 255, 255, 0.7);
    border-radius: 50%;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    -webkit-backdrop-filter: blur(10.22760009765625px);
    backdrop-filter: blur(10.22760009765625px);
    --webkit-backdrop-filter: blur(10.22760009765625px);
    background-color: rgba(56, 56, 56, 0.75);
    text-shadow: 0px 0px 11px #8888888c;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.cardPreview .icon.watch {
    width: 45px;
    padding-left: 5px;
}

.cardPreview .icon.read {
    font-size: 26px;
}

.cardPreview .icon.course {
    font-size: 28px;
}


/* COMPACT CARD STYLING */
.compactCard {
    max-width: 95%;
    width: 674px;
    height: 200px;
    border-radius: 8px;
    background: #fff;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);

    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.compactCardContent {
    flex-grow: 1;
    display: flex;
}

.compactCardPreview {
    padding: 20px 10px 0 5px;
}

.compactCardPreview img{
    width: 16vw;
    max-width: 120px;
    height: 120px;
    border-radius: 9px;
    object-fit: cover;
}

.compactCardPreview .icon {
    top: 125px;
    width: 16vw;
    max-width: 120px;
    height: 15px;
    line-height: 15px;
    font-size: 12px;
    border-radius: 0 0 9px 9px;
    right: 10px;
}

.compactCardPreview .icon.watch {
    width: 16vw;
    max-width: 120px;
    padding-left: 0;
}

.compactCardPreview .icon.read {
    font-size: 12px;
}

.compactCardPreview .icon.course {
    font-size: 13px;
}
.cardViewModal {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.modalHeader {
    position: relative;
    width: 100%;
    height: 51px;
    border-radius: 13px 13px 0px 0px;
    background-color: #F6F6F6;
    border-bottom: solid rgba(0, 0, 0, 0.3) 0.5px;
}

.modalHeader h2 {
    position: absolute;
    width: 100%;
    text-align: center;
    font-weight: 600;
    font-size: 17px;
    letter-spacing: -0.03em;
}

.closeModalButton {
    position: absolute;
    right: 20px;
    height: 100%;
    font-weight: 600;
    font-size: 17px;
    letter-spacing: -0.02em;
    text-align: left;
    color: #2dbfbf;
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
}

.cardViewModalContent {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 10px 5px;
}

.cardViewModalContent .icon {
    font-size: 36px;
    padding-right: 20px;
    color: #BBBBBA;
}

.cardViewModalContent button {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 17px;
    color: #74746e;
    text-align: left;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0 20px;
    flex-grow: 1;
}

.cardViewModalContent .active, .cardViewModalContent .active > .icon{
    color: #2dbfbf;
}
.expandedViewScrollContainer {
    position: fixed;
    width: 750px;
    max-width: 90%;
    margin: 0 auto;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    -ms-scroll-chaining: none;
        overscroll-behavior: contain;
    scrollbar-width: none;
}

.expandedViewScrollContainer::-webkit-scrollbar {
    display: none;
}

.expandedViewContentContainer {
    position: absolute;
    width: 100%;
    margin-top: 100px;
    margin-bottom: 100px;
    border-radius: 13px;
    background-color: white;
}

.Modal.expandedView {
    position: relative;
    top: 50px;
    width: 750px;
    max-width: 90%;
    height: auto;
    margin: 0 auto;
}

.expandedViewContentContainer .overlayButton {
    width: 35px;
    height: 35px;
    padding: 0;
    -webkit-backdrop-filter: blur(0px);
    backdrop-filter: blur(0px);
    --webkit-backdrop-filter: blur(0px);
    background-color: #2DBFBF;
    border-radius: 50%;
    color: #E6FAF9;
    font-size: 20px;
    box-sizing: border-box;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
        user-select: none;
    outline: none;
    border: none;
    cursor: pointer;
}

.expandedViewContentContainer .closeButton {
    position: absolute;
    font-size: 28px;
    line-height: 28px;
    top: 5px;
    left: 5px;
    background-color: rgba(255, 255, 255, 0.75);
    color: #727272;
}

.expandedViewContentContainer .closeButton:focus {
    border: solid 1px #2dbfbf;
}

.expandedViewActionButtons {
    position: absolute;
    top: 5px;
    right: 5px;
    display: flex;
    justify-content: space-between;
    width: 120px;
}

/* Image area container */
.expandedViewImageArea{
    border-radius: inherit;
    height: 300px;
}

.expandedView img {
    width: 100%;
    object-fit: cover;
    height: 100%;
    border-radius: inherit;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

.expandedViewTitleArea {
    display: flex;
    background-color: #f6f6f6;
    border-bottom: 1px solid #d0d4d8;
}

.expandedViewTitleArea div:first-child {
    flex-grow: 2;
}

.expandedViewTitleArea h1 {
    margin: 0;
    padding: 10px 0 0 10px;
    line-height: 36px;
}

.expandedViewTitleArea h3 {
    margin: 0;
    padding: 0 0 10px 10px;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.9);
}

.startResourceButton {
    min-width: 125px;
    height: 36px;
    margin: 15px;
    align-self: center;
    border-radius: 18px;
    background: #2dbfbf;
    font-weight: 500;
    font-size: 25px;
    color: #E7F9F8;
    text-align: center;
    border: none;
    cursor: pointer;
}

.expandedViewContentContainer h2 {
    margin: 10px 0 0 10px;
}

.expandedViewContentContainer p {
    margin: 0 0 0 10px;
}

/* #region Media Queries */

@media only screen and (max-width: 500px), screen and (max-height: 500px) {

    .expandedViewTitleArea {
        position: relative;
    }

    .startResourceButton {
        position: absolute;
        top: -65px;
        right: 0;
    }
    
}

/* #endregion */
